<template>
     <!-- 数据为加载完成时候显示 -->
     <div>
         <div class="loading" v-if='isLoading'>
            <img src="../assets/loading.gif" alt="">
        </div>
        <div class="userInfomation" v-else>
            <section>
                <img :src="user_informations.avatar_url" alt="">
                <span>{{user_informations.loginname}}</span>
                <p>
                    {{user_informations.score}}积分
                </p>
                <p>
                   注册时间： {{user_informations.create_at |formDate}}
                </p>
            </section>
            <div class="topics">
                <p>
                    最近创建的话题
                </p>
                <div>
                    <ul>
                        <li v-for='(item,index) in titleLists' :key='index'>
                            <img :src="item.author.avatar_url" >
                            
                            <router-link :to='{
                                name:"post_content",
                                params:{
                                    id:item.id,
                                    limit:1,
                                    page:1
                                }
                            }'>{{item.title}}</router-link>
                        </li>
                    </ul>
                    <div class="more">
                        <router-link :to="{
                            name:'more',
                            pramas:{
                                name:this.user_informations.loginname
                            }
                        }">查看更多>></router-link>
                    </div>
                </div>
            </div>
            <div class="replies">
                <p>最近参与的话题</p>
                <div>
                    <ul>
                        <li v-for='(item,index) in repliesLists' :key='index'>
                            <img :src="item.author.avatar_url" >
                            
                            <router-link :to='{
                                name:"post_content",
                                params:{
                                    id:item.id,
                                    limit:1,
                                    page:1
                                }
                            }'>{{item.title}}</router-link>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
     </div>
        
</template>

<script>
export default {
    name:'UserInfo',
    data(){
        return {
            isLoading:false,
            user_informations:{},
            titleLists:[],
            repliesLists:[]
        }
    },
    methods:{
        getUserData(){
            this.$http.get('https://cnodejs.org/api/v1/user/'+this.$route.params.name)
            .then((res)=>{
                
                // console.log(res)
                if(res.data.success===true){
                    this.isLoading=false
                    this.user_informations=res.data.data
                    this.repliesLists=res.data.data.recent_replies
                    if(res.data.data.recent_topics.length>5){
                        this.titleLists=res.data.data.recent_topics.splice(0,5)
                    }else{
                         this.titleLists=res.data.data.recent_topics
                    }
                }
            })
            .catch((err)=>{
                console.log(err)
            })
        },
    },
    beforeMount(){
        this.isLoading=true
        this.getUserData()
    }

}
</script>

<style scoped>



.loading {
    text-align: center;
    padding-top: 300px;
  }
ul li a {
     color: #53afdd;
    text-decoration: none;
}
ul li a:hover {
      color: #005580;
  }
.userInfomation {
    background: white;
    width: 75%;
    margin: 10px auto;
  }
  
  .userInfomation section {
    padding: 12px;
  }
  .userInfomation img {
    width: 30px;
  }
  .userInfomation li {
    list-style:none;
  }
  .userInfomation .replies,
  .userInfomation .topics {
    font-size: 0.72rem;
    border-top: 10px #DDDDDD solid;
    position: relative;
  }
  .userInfomation  p {
    padding: 5px;
    font-size: 0.75rem;
    margin: 0;
  }
  .userInfomation  ul > li {
    padding: 4px 0 4px 12px;
    white-space: nowrap;
    font-size: 0.72rem;
    text-overflow: ellipsis;
    overflow: hidden;
    line-height: 30px;
    vertical-align: middle;

  }
 .userInfomation .more {
      position: absolute;
      right: 0;
      top: 91%;
      font-size:0.8rem;
      
  }
.more a {
    color:#666;
    text-decoration: none;
}
.more a:hover {
    color:#385f8a;
}
</style>


