<template>
    <div class="header">
        <div class="header-inner">
            <router-link :to="{name:'root'}"> <img src="../assets/cnodejs_light.svg" alt=""></router-link> 
        </div>
    </div>
</template>

<script>
    export default {
        name:'Header',

    }
</script>

<style scoped>
.header {
    background: #445b55;
    width: 100%;
    height: 50px;
    font-size:13px;
    position: relative;
}
.header-inner {
    border-radius: 0;
    border: none;
    box-shadow: none;
    width: 90%;
    margin: auto;
    padding: 5px;
    text-align: center;
}
.header-inner a {
    width: 120px;
    padding: 3px 20px;
    height: 34px;
    line-height: 34px;
    color: #ccc;
    font-weight: 700;
    text-shadow: none;
    text-decoration: none;
    
}
a>img {
    max-width: 120px;
    vertical-align: initial;
}
</style>


